.form {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr;
  gap: 20px;
  padding: 30px;
  margin-right: 40px;

  border-radius: 30px;
  background-color: var(--accent-bg-color);
  color: var(--accent-text-color);

  @include tablet {
    gap: 0;
  }

  &__row {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;
    margin-bottom: 15px;
    @include mobile {
      grid-template-columns: repeat(1, 1fr);
      gap: 10px;
    }
  }

  &__title {
    font-size: 28px;
    line-height: 150%;
    margin-bottom: 5px;
  }

  &__btn {
    width: auto;
    padding-left: 10px;
    padding-right: 10px;
  }
}

.form-field {
  display: block;
  height: 50px;
  padding: 5px 10px;
  border-radius: 10px;
  border: 1px solid var(--accent-text-color);

  &__caption {
    opacity: 0.6;
    font-size: 12px;
    line-height: 150%;
  }

  &__input {
    width: 100%;
    border: transparent;
    background-color: transparent;
    color: var(--accent-text-color);
  }
}
