.footer {
  background-color: var(--header-color);
  color: var(--accent-text-color);
  font-size: 16px;
  line-height: 150%;

  &__container {
    padding-top: 50px;
    padding-bottom: 10px;
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    gap: 50px;
    @include tablet {
      grid-template-columns: repeat(5, 1fr);
      gap: 20px;
    }
    @include small-tablet {
      grid-template-columns: repeat(3, 1fr);
      gap: 10px;
      padding: 30px;
    }
    @include mobile {
      grid-template-columns: repeat(1, 1fr);
    }
  }

  &__info {
    grid-column: span 2;
    @include tablet {
      grid-column: span 2;
    }
    @include small-tablet {
      grid-column: span 3;
    }
    @include mobile {
      grid-column: span 1;
    }
}

  &__title {
    padding-bottom: 20px;
    font-size: 22px;
    line-height: 150%;
    font-weight: 700;

    @include tablet {
      padding-bottom: 0;
      font-size: 18px;
    }
  }

  &__link {
    color: var(--link-color);
    transition: color 0.3s ease-in-out;

    @include hover {
      color: var(--bg-color);
    }
  }

  &__logo {
    display: block;
    text-align: center;
    img{
      min-width: initial;
      width: 150px;
    }
  }
}

