.side-block {
  align-self: start;
  padding: 20px 30px 30px;
  border-radius: 30px;
  background-color: var(--accent-bg-color);
  color: var(--accent-text-color);

  @include mobile {
    padding: 15px;
  }

  &__title {
    margin-bottom: 30px;
    font-size: 28px;
    line-height: 150%;
    @include tablet {
      margin-bottom: 0;
    }
  }

  &__item {
    margin-bottom: 15px;
  }

  &__btn {
    display: inline-block;
    width: 270px;
    height: 50px;
    line-height: 50px;
    @include mobile {
      width: auto;
    }
  }
}

.side-item {

  &__title {
    font-size: 18px;
    line-height: 150%;
  }

  &__info {
    font-size: 16px;
    line-height: 150%;
  }
}

