.dark-theme{

    // colors
    --bg-color: #313D5B;
    --title-color: #ec86ac;
    --text-color: #cddafd;
    --link-color: #fad2e1;
    --header-color: #ec86ac;
    --btn-text-color: #eff7f8;
    --btn-reflex-color: #fdc4e3;
    --accent-bg-color: #cddafd;
    --accent-text-color: #313D5B;

    --bg-img-1: url("../img/hero-bg-1-dark.jpg");
    --bg-img-2: url("../img/hero-bg-2-dark.jpg");
    --theme-switcher: url("../img/sun.svg");

    .theme-switcher--moon {
      display: none;
    }
    .theme-switcher--sun {
      display: initial;
    }
}
