.basket {
  margin-top: 100px;
  align-self: start;
  color: var(--text-color);

  &__container {
    padding-top: 70px;
    padding-bottom: 140px;
    @include tablet {
      padding-top: 40px;
      padding-bottom: 50px;
    }
    @include mobile {
      padding-top: 20px;
      padding-bottom: 30px;
    }
  }

  &__wrapper {
    display: grid;
    grid-template-columns: auto 330px;
    gap: 20px;
    @include small-tablet {
      grid-template-columns: auto;
    }
  }

  &__list {
  display: grid;
  grid-template-columns: 1fr;
  gap: 20px
  }
}

.basket-item {
  display: grid;
  grid-template-columns: 80px auto 80px 120px 30px;
  gap: 10px;
  align-items: center;
  color: var(--accent-text-color);
  background-color: var(--accent-bg-color);
  border-radius: 30px;
  padding: 15px 15px;

  @include tablet {
    grid-template-columns: 80px 80px 80px auto auto;
  }
  @include mobile {
    grid-template-columns: 80px 80px auto auto auto;
  }

  &__img {
    height: 80px;
    background-color: var(--btn-bg-color);
    border-radius: 10px;


    img {
      display: block;
      border-radius: 10px;
      border: 4px solid var(--btn-bg-color);
    }
  }

  &__title {
    font-size: 28px;
    line-height: 120%;
    color: var(--accent-text-color-color);
    display: -webkit-box;

    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
    transition: opacity 0.3s ease-in-out;

    @include hover {
      opacity: 0.7;
    }

    @include tablet {
      grid-column: 4 span;
      font-size: 24px;
    }
  }

  &__number {
    @include flex-v-center;
    height: 30px;
    border-radius: 5px;
    border: 1px solid var(--accent-text-color);
    opacity: 0.5;
  }

  &__btn {
    height: 30px;
    width: 30px;
    color: var(--accent-text-color-color);
    font-size: 18px;
    font-weight: 700;
    opacity: 0.5;
    transition: opacity 0.3s ease-in-out;

    @include hover {
      opacity: 1;
    }

    &--del {
      opacity: 0.5;
      position: relative;
      transition: opacity 0.3s ease-in-out;

      @include hover {
        opacity: 1;
      }
      &::before {
        position: absolute;
        top: 4px;
        left: 50%;
        content: '';
        height: 20px;
        width: 1px;
        background-color: var(--accent-text-color);
        transform: rotate(45deg);
      }
      &::after {
        position: absolute;
        top: 4px;
        left: 50%;
        content: '';
        height: 20px;
        width: 1px;
        background-color: var(--accent-text-color);
        transform: rotate(-45deg);
      }

    }
  }

  &__value {
    width: 30px;
    font-size: inherit;
    text-align: center;
    color: inherit;
    background-color: transparent;
  }

  &__price {
    font-size: 24px;
    width: 100px;
  }
}

.basket-empty {
  grid-column: span 2;
  height: 100%;
  padding: 35px;
  border-radius: 50px;
  background-color: var(--accent-bg-color);
  color: var(--accent-text-color);
  text-align: center;
  &__title {
    padding: 20px 0 30px;
  }
  &__message {
    margin-bottom: 30px;
  }
  &__btn {
    display: inline-block;
    height: 60px;
    padding-left: 26px;
    padding-right: 26px;
    line-height: 60px;
  }
}


