.header {
  position: fixed;
  z-index: 10;
  width: 100%;
  box-shadow: 0px 1px 10px var(--bg-color);
  background-color: var(--header-color);
  &__container {
    @include flex-v-center;
    justify-content: space-between;
  }

  &__logo {
    font-size: 0;
    img {
      max-height: 90px;
    }
  }

  &__menu {
    display: flex;
  }

  &__link {
    position: relative;
    font-size: 0;
    margin-left: 40px;

    @include mobile {
      margin-left: 10px;
    }
  }
}

.cart {
  &__svg {
    fill: var(--link-color);
    @include hover {
      fill: var(--bg-color)
    }
  }
  &__quantity {
    position: absolute;
    right: 0;
    bottom: 0;
    height: 25px;
    width: 25px;
    padding-top: 4px;
    color: var(--accent-bg-color);
    background-color: var(--accent-text-color);
    border-radius: 50%;
    font-size: 16px;
    font-weight: 700;
    text-align: center;
    &--empty {
      display: none;
    }
  }
}

.theme-switcher {
    width: 45px;
    height: 70px;
    background-position: center;
    background-repeat: no-repeat;
    background-size: 40px;
    &--moon {
      background-image: url("../img/moon.svg");
      @include hover {
        background-image: url("../img/moon-hover.svg");
      }
    }
    &--sun {
      display: none;
      background-image: url("../img/sun.svg");
      @include hover {
        background-image: url("../img/sun-hover.svg");
      }
    }
}
