.catalog {
  padding-top: 90px;

  @include mobile {
    margin-top: 40px;
  }
}

.product-cards {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  @include big-desktop {
    grid-template-columns: 1fr 1fr 1fr;
  }
  @include tablet {
    grid-template-columns: 1fr 1fr;
    gap: 20px;
  }
  @include mobile {
    grid-template-columns: 1fr;
  }
}

.product-card {
  height: 500px;
  position: relative;
  display: grid;
  grid-template-rows: 1fr 1fr auto 2fr;
  padding: 30px 20px 0px;
  border-radius: 50px;
  text-align: center;
  transition: 0.1s ease-in-out;


  @include hover {
    background-color: var(--accent-bg-color);
    grid-template-rows: 1fr 1fr auto 1fr 1fr;
    .product-card__text {
      color: var(--accent-text-color);
    }
    .product-card__btn {
      display: block;
    }
  }

  &__img {
    display: block;
    width: 230px;
    height: 230px;
    overflow: hidden;
    margin: 0 auto 10px;

    img {
      height: 100%;
      width: 100%;
      border-radius: 50px;
      border: 12px solid var(--btn-bg-color);
    }
  }

  &__title {
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
    color: var(--btn-bg-color);
    font-size: 28px;
    line-height: 120%;
  }

  &__text {
    color: var(--text-color);
    font-size: 22px;
    line-height: 150%;
  }

  &__price {
    color: var(--btn-bg-color);
    font-size: 28px;
    line-height: 150%;
    span {
      font-size: 32px;
      line-height: 150%;
    }
  }

  &__btn {
    height: 40px;
    display: none;
    margin: 0 auto;
    align-self: start;
  }
  @include tablet {
    background-color: var(--accent-bg-color);
    grid-template-rows: 1fr 1fr auto 1fr 1fr;

    .product-card__text {
      color: var(--accent-text-color);
    }
    .product-card__btn {
      display: block;
    }
  }

  &:disabled {
    opacity: 0.4;
    pointer-events: none;
  }
}

