.ordering {
  align-self: start;
  margin-top: 100px;

  &__container {
    padding-top: 30px;
    padding-bottom: 100px;
    @include mobile {
      padding-top: 0px;
      padding-bottom: 40px;
      }
  }

  &__wrapper {
    display: grid;
    grid-template-columns: auto 400px;
    gap: 20px;
    @include tablet {
      grid-template-columns: 1fr;
    }
  }

  &__form{
    display: block;
    height: fit-content;
    @include tablet {
      grid-row-start: 2;
    }
  }

  &__btn{
    margin-top: 15px;
  }

  &__side {
    @include mobile {
      padding: 25px;
    }
  }
  &__list{
    @include tablet {
      display: grid;
      grid-template-columns: 1fr 1fr;
    }
    @include mobile {
      grid-template-columns:1fr;
    }
  }
}

