.feedback {
  margin-top: 100px;

  &__header {
    display: flex;
    justify-content: space-between;
    @include mobile {
      display: block;
    }
  }

  &__title {
    @include mobile {
      margin-bottom: 0;
    }
  }

  &__link-all {
    @include flex-v-center;
    color: var(--link-color);
    transition: color 0.3s ease-in-out;

    @include hover {
      color: var(--accent-text-color);
    }
    @include mobile {
      padding-left: 10px;
    }
  }

  &__list {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    gap: 20px;
    @include tablet {
      grid-template-columns: 1fr 1fr;
    }
    @include mobile {
      grid-template-columns: 1fr;
    }
  }
  @include mobile {
    margin-top: 40px;
  }

}

.feedback-item {
  padding-top: 40px;
  &__wrapper {
    height: 100%;
    background-color: var(--accent-bg-color);
    border-radius: 50px;
    margin: 0;
    position: relative;
    padding: 40px 20px 40px;
  }

  &__img {
    position: absolute;
    top: -40px;
    left: 50%;
    transform: translate(-50%, 0);
    height: 80px;
    border-radius: 50%;
    overflow: hidden;
  }

  &__text {
    margin: 0;
    padding: 0;
    font-size: 18px;
    line-height: 150%;
    color: var(--accent-text-color);
    display: -webkit-box;
    -webkit-line-clamp: 5;
    -webkit-box-orient: vertical;
    overflow: hidden;
  }

  &__figure {
    position: absolute;
    right: 50px;
    bottom: 13px;
    font-size: 16px;
    line-height: 150%;
    text-align: right;
    color: var(--accent-text-color);

    &::before {
      content: 'â€” ';
    }
  }
}
