.hero {
  padding-top: 90px;

  &__container {
    display: flex;
    height: calc(100vh - 180px);
    @include flex-all-sa;
    @include mobile {
      flex-wrap: wrap-reverse;
      height: initial;
    }
  }

  &__content {
    max-width: 600px;
    @include tablet {
      padding: 0 20px;
    }
    @include mobile {
      text-align: center;
    }
  }

  &__title {
    font-size: 96px;
    margin-bottom: 20px;
    line-height: 100%;
    @include tablet {
      margin-bottom: 20px;
      font-size: 76px;
    }
    @include small-tablet {
      font-size: 44px;
    }
    @include mobile {
      font-size: 36px;
    }
  }

  &__btn {
    margin-top: 50px;
    height: 60px;
    min-width: 290px;
    display: inline-block;
    line-height: 60px;
    @include mobile {
      margin-top: 10px;
      height: 40px;
      line-height: 40px;
    }
  }

  &__img {
    max-width: 400px;
    min-width: 200px;
    border-radius: 25px;
    overflow: hidden;
    border: solid 20px;
    border-color: var(--btn-bg-color);
    background-color: var(--btn-bg-color);

    @include mobile {
      margin: 20px 0;
      height: 50vh;
    }

    img {
      display: block;
    }
  }
}
