html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;

  background-color: var(--bg-color);
  background-image: var(--bg-img-1), var(--bg-img-2), var(--bg-img-2);
  background-repeat: no-repeat;
  background-position: left calc(50% - 50vw) top -1650px, right calc(90% - 40vw) top 700px, left calc(90% - 30vw) top 1700px;
}

img {
  height: auto;
  min-width: 100%;
  min-height: 100%;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

h1, h2, h3 {
  margin: 0;
}
h2 {
  padding-left: 50px;

  @include tablet {
    padding-left: 20px;
  }
  @include mobile {
    padding-left: 10px;
  }
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active  {
  -webkit-text-fill-color: var(--accent-text-color) !important;
  -webkit-box-shadow: 0 0 0 30px var(--accent-bg-color) inset !important;
}

.site-container {
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 100vh;
}


.is-hidden {
  display: none !important;
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;
  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.title {
  color: var(--title-color);
  font-weight: 700;
  font-size: 72px;
  line-height: 120%;
  margin-bottom: 30px;

  @include tablet {
    font-size: 64px;
  }
  @include small-tablet {
    font-size: 54px;
  }
  @include mobile {
    font-size: 48px;
    margin-bottom: 10px;

  }

}

.text {
  color: var(--text-color);
  font-weight: 700;
  font-size: 22px;
  line-height: 150%;
  @include tablet {
    font-size: 20px;
  }
  @include small-tablet {
    font-size: 18px;
  }
  @include mobile {
    font-size: 16px;
  }

}

.btn {
  height: 45px;
  color: var(--btn-text-color);
  font-weight: 700;
  font-size: 24px;
  line-height: 150%;
  background-color: var(--btn-bg-color);
  background-image: linear-gradient(0deg, rgba(0, 0, 0, 0) 0%, var(--btn-reflex-color) 100%);

  padding-left: 30px;
  padding-right: 30px;
  border-radius: 30px;
  transition: background-color 0.3s ease-in-out;


  @include hover {
    background-color: var(--btn-hover-color);
  }

  @include mobile {
    font-size: 18px;
  }
}
