.notification {
  position: absolute;
  top: 120px;
  left: 50%;
  transform: translate(-50%, 0);
  width: 150px;
  height: 50px;
  background-color: var(--accent-bg-color);
  color: var(--accent-text-color);
  text-transform: uppercase;
  font-weight: 700;
  font-size: 20px;
  line-height: 50px;
  text-align: center;
  border-radius: 25px;
  opacity: 0;
  transition: opacity 0.5s ease-in-out;
}

.notification--active {
  .notification {
    opacity: 1;
  }
}
