.product {
  margin-top: 100px;

  @include mobile {
    margin-top: 80px;
  }

  &__container {
    padding-top: 70px;
    padding-bottom: 100px;

    @include tablet {
      padding-top: 20px;
      padding-bottom: 40px;

    }
  }

  &__title {
    margin-top: 30px;
    @include mobile {
      margin-top: 0px;
      line-height: 120%;
    }

  }
}

.product-item {
  display: flex;
  justify-content: space-between;
  padding: 50px;
  background-color: var(--accent-bg-color);
  border-radius: 50px;
  color: var(--accent-text-color);

  @include small-tablet {
    flex-direction: column;
    padding: 30px 70px;

  }
  @include mobile {
    padding: 30px 30px;
  }

  &__img {
    margin: 0 auto;
    position: relative;
    img {
      display: block;
      border-radius: 25px;
      overflow: hidden;
      border: solid 20px;
      border-color: var(--btn-bg-color);
    }
  }

  &__descr {
    max-width: 50%;
    padding-left: 40px;

    @include small-tablet {
      max-width: 100%;
      padding-left: 0;
    }
  }

  &__price {
    font-size: 48px;
    line-height: 150%;
    font-weight: 700;
    padding-bottom: 25px;
    @include small-tablet {
      text-align: center;
    }
    @include mobile {
      padding-bottom: 0;
    }

  }

  &__text {
    padding-bottom: 20px;
    position: relative;

    @include small-tablet {
      line-height: 150%;
      padding-bottom: 0;

    }

    svg {
      position: absolute;
      top: -2px;
      left: 0;
      path {
        stroke: currentColor;
      }
    }

    p {
      margin: 0;
      padding-left: 40px;
    }
  }

  &__btn {
    width: 100%;
  }
}
